import React, {useState, useEffect} from 'react';
import questionImg from "../public/static/img/icon/question.png";
import warningImg from "../public/static/img/icon/warning.png";
import "./modal.css"

interface ModalProps {
    isOpen: boolean;
    onClose: () => void;
    modalContent: string;
    modalType: "warning" | "success" | "question";
}

const Modal: React.FC<ModalProps> = ({isOpen, onClose, modalContent,modalType}) => {
    const [modalVisible, setModalVisible] = useState(isOpen);

    const closeModal = () => {
        setModalVisible(false);
        onClose();
    };

    useEffect(() => {
        setModalVisible(isOpen)
    }, [isOpen])


    return (
        <>
            {modalVisible && (
                <div className="modal-div" onClick={closeModal}>
                    <div className={`modal-content ${isOpen ? 'fade-in' : ''}`} onClick={(e) => e.stopPropagation()}>
                        <img className="modal-img" src={modalType === "warning" ? warningImg : questionImg} alt={'modal img'} width={60} height={60}/>
                        <p>{modalContent}</p>
                        <button onClick={closeModal} className="save-button">확인</button>
                    </div>
                </div>
            )}
        </>
    );
};

export default Modal;