import React,{useState} from 'react';
import Sub from './MenuSub.js';

function MenuTopChild(props){
    const [isOpen, setMenu] = useState(false);

    const toggleMenu = () => {
        setMenu(isOpen => !isOpen);
    }
    return (
        isOpen !== false ? (
        <li className="menu-item open active" key={props.menu.id}>
            <a className="menu-link menu-toggle" onClick={() => toggleMenu()}>
                <i className="menu-icon tf-icons bx bx-layout"></i>
                <div key={props.menu.name+props.menu.id}>{props.menu.name}</div>
            </a>
            <ul className="menu-sub">
                {props.menu.child.map((menuSub, index) => (
                <Sub key={index} menu={menuSub}/>
                ))}
            </ul>
        </li>
        ) : (
            <li className="menu-item" key={props.menu.id}>
                <a className="menu-link menu-toggle" onClick={() => toggleMenu()}>
                    <i className="menu-icon tf-icons bx bx-layout"></i>
                    <div key={props.menu.name+props.menu.id}>{props.menu.name}</div>
                </a>
                <ul className="menu-sub">
                    {props.menu.child.map((menuSub, index) => (
                        <Sub key={index} menu={menuSub}/>
                    ))}
                </ul>
            </li>
        )
    );
}

export default MenuTopChild;