import {useNavigate} from "react-router-dom";
import {useState} from "react";
import CryptoJS from "crypto-js";
import axios from "axios";
import Modal from "../common/Modal"
import './loginForm.css'
import KMSICON from '../public/static/img/icon/i-kms.png';

function Login(){

    const [id, setId] = useState("");
    const [pw, setPw] = useState("");
    const secretKey = process.env.REACT_APP_SECRET_KEY;
    const iv = process.env.REACT_APP_IV_KEY;

    const encrypt = (text: string) => {
        const cipher = CryptoJS.AES.encrypt(text, CryptoJS.enc.Utf8.parse(secretKey), {
            iv: CryptoJS.enc.Utf8.parse(iv),
            padding: CryptoJS.pad.Pkcs7,
            mode: CryptoJS.mode.CBC,
        });

        return cipher.toString();
    }
    const hashFunction = (e) => {
        e.preventDefault();

        const data = {
            id,
            pw,
        };
        const encrypted = encrypt(pw);
        const params = new URLSearchParams();
        params.append('loginId', id);
        params.append('cryptoData', encrypted);
        const fetchData = async() => {
            const url = '/api/kms/login';
            const res = await axios.post(url,null,{params:params});
            return res;
        }
        fetchData().then(res =>
            // console.log(res)
                res.data.payload.success === false ? openModal(res.data.payload.errorMsg,"warning") : getToken(res.data.payload.tokenPreValue)
        );
    };

    const [modalOpen, setModalOpen] = useState(false);
    const [modalContent, setModalContent] = useState("");
    const [modalType, setModalType] = useState("");
    const openModal = (content,modalType) => {
        setModalOpen(true);
        setModalType(modalType);
        setModalContent(content);
    };

    const closeModal = () => {
        setModalOpen(false);
    };
    const getToken=async(preValue)=>{
        const params = new URLSearchParams();
        params.append('loginId', "empty");
        params.append('cryptoData', preValue);
        const url = '/api/kms/getKmsToken';
        const res = await axios.post(url,null,{params : params});
        addStorage(res.data.payload.kmsToken)
    }
    const addStorage = (token) =>{
        localStorage.setItem("mrKmsToken",token);
        window.location.href = "/";
    }
    return (
        <>
            <div className="loginFormMain">
                <div>
                    <h2 className="loginFormTitle">지식관리시스템 로그인</h2>
                </div>
                <div className="loginFormImgArea"><img className="loginFormImg" src={KMSICON} alt=""/></div>
                <form onSubmit={(e) => hashFunction(e)}>
                    <div className="loginInputDiv">
                        <span className="loginFormTtl">아이디</span>
                        <input className="loginInputBox" type="text" value={id} onChange={(e) => setId(e.target.value)} />
                    </div>
                    <div className="loginInputDiv">
                        <span className="loginFormTtl">비밀번호</span>
                        <input className="loginInputBox" type="password" value={pw} onChange={(e) => setPw(e.target.value)}/>
                    </div>
                    <div className="loginBtnArea">
                    <button className="loginBtn" type="submit">로그인</button>
                    </div>
                </form>
            </div>
            <Modal isOpen={modalOpen} onClose={closeModal} modalContent={modalContent} modalType={modalType}/>
        </>
    );
}
export default Login;